import React from 'react';
import styles from "./About.module.css";
export default function AboutEstablishment() {
  return (
    <>
      <div className={styles.text}>
        <h3>The establishment of the College :</h3>
        <p>The establishment of the College of Applied Arts dates back to Presidential Decree No. 342 of 6996, which established the 6th of October University and included the creation of the College of Applied Arts. The study at the College of Applied Arts began in the academic year 3222/6999 and continues until now. The college has witnessed an increasing demand from Egyptian and Arab students to meet the needs of the job market.
        The academic degree for the undergraduate level (Bachelor's degree) offered by the college was equivalenced for the following departments: Photography, Cinema and Television, Advertising, Interior Design and Furniture, in accordance with the semester regulations adopted by Helwan University's College of Applied Arts. This equivalency was granted based on the ministerial decree issued by the Minister of Higher Education and State for Scientific Research, numbered 343 of 3224/62/62, and the renewal of equivalency number 6 in 3222/6/32.
        <br />
        <br />
        Furthermore, the academic degree offered by the college was equivalenced again, as per the ministerial decree issued by the Minister of Higher Education and State for Scientific Research, numbered 66 of 3266/3/62, for the departments of Photography, Cinema and Television, Advertising, Interior Design and Furniture.
        Additionally, the academic degree offered by the college was equivalenced once again, based on the ministerial decree issued by the Minister of Higher Education and State for Scientific Research, numbered 56 of 3264/3/62, for the departments of Photography, Cinema and Television, Advertising, Interior Design and Furniture, and the Department of Product Design.
        </p>
      </div>
    </>
  );
}
